.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0ox 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: 500px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    display: flex;
    justify-content: center;
    align-items: center;    
    min-height: 24px;
    width: 24px;
    margin: 12px; 
    border-radius: 50px;
}

.LoadingTodo-completeIcon {

    left: 12px;
}

.LoadingTodo-deleteIcon {    
    top: 0;
    right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {   
    background-image: linear-gradient(to right,#f2f2f2 0%,#f2f2f2 30%,#d9d9d9 45%, #d9d9d9 50%,#f2f2f2 60%,#f2f2f2 100%);
    background-size: 400% 400%;
    animation: loadingAnimation 1s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 100%;
    }    
    100% {
        background-position: 0%;
    }
}