.CreateTodoButton {
    position: relative;
    height: 64px;
    width: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;        
    font-weight: bold;    
    border: none;
    border-radius: 50%;
    background-color: var(--clr-primary3);
    color: #FAFAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    cursor: pointer;
    transform: rotate(0);
    transition: .3s ease;
}

.CreateTodoButton:hover {
    transform: rotate(224deg);
}